// Men√∫ superior izquierdo

.hero__menu {
    height: 44px;
    width: 44px;
    position: fixed;
    top: 0;
    left: 0; 
}

.hero__menu-img {
    width: 16px;
    height: 14px;
    object-fit: contain;
    background-color: $color-blue;
    $color-white: $color-white;
    position: fixed;
    top: 0;
    left: 0;
}

// Hero - Anonymous 

.page__header {
    height: 100vh;
    background-image: url("/assets/images/cover.jpg");
    background-position: center;
    background-size: cover;
    top: 0;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.hero__container {
    transform: translateY(-20%)
}

.hero__anonymous {
    margin: 0;
    color: $color-white;
    font-size: 22px;
    font-family: 'Roboto Slab', serif;
    text-transform: uppercase;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.hero__anonymous-text {
    margin: 0;
}

// Hero - Habitasse

.hero__habitasse {
    width: 100%;
    padding-top: 10px; 
}

.hero__habitasse-text {
    width: 100%;
    color: $color-white;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    font-weight: normal;
}

// Hero -Button

.botton__down-container {
   display: flex;
   justify-content: center;
   align-content: center;
   padding-top: 45px;
}

.hero__button-down {
    background-color: $color-white;
    border-radius: 50%;
    border: 12px solid white;    
}

.hero__button-down-img {
    transform: translateY(3px)
  
}